@import "src/common/styles/variables/variables";

.mainBlock {
  background-color: $backGroundColor;
  min-height: 75vh;

  .particles {
    position: absolute;
    top: 0;
    border-top: 0;
    right: 0;
    left: 0;
    opacity: 0.3;
  }

  .text {
    font-family: 'Josefin Sans', sans-serif;
    letter-spacing: 1px;
    font-size: 20px;
    margin: 0 0 5px;
    line-height: 1.2;
    color: $textColor;
    background-color: $backSecondGroundColor;
    width: 300px;
    border-radius: 5px;
  }

  .photo {
    width: 350px;
    height: 400px;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 5px;
    margin-top: 15px;
  }
}


